import React from "react"
import { useStaticQuery, graphql } from "gatsby"

export default function Header() {
    const data = useStaticQuery(
        graphql`
            query {
                site {
                    siteMetadata {
                        title
                    }
                }
            }
        `
    )
  return (
      <div className="header-main">
      <h1>{data.site.siteMetadata.title}</h1>
      </div>
      )
}
